
import Task from './Task'
const Tasks = ({tasks , onDelete , onToggle}) => {


    


    return (
        <>
            {tasks.map((task) =>(
                <Task key={task.id} 
                task={task}
                 onDelete={onDelete} 
                 onToggle={onToggle}/>
            ))}

            <p style={{color: 'red'}}> Double click to toggle Reminder</p>
        </>
    )
}

export default Tasks;