import React from 'react';
import './index.css'
import Header from './components/Header'
import Tasks from './components/Tasks';
import {  useState} from 'react'
import AddTask from './components/AddTask'


function App() {


    const [showAddTask, setShowAddTask] = useState(false)

	const [tasks, setTasks] = useState(
        [
              {
                  id : 1,
                  text: "Doctor Appointment",
                  day: 'Feb 5th at 2:30pm',
                  reminder: true
              },
              {
                  id : 2,
                  text: "Vendor Appointment",
                  day: 'Feb 8th at 2:30pm',
                  reminder: false
              },
              {
                  id : 3,
                  text: "Colombo Appointment",
                  day: 'Feb 10th at 2:30pm',
                  reminder: false
              }
          ]
      )


      //Add Task
      const addTask = (task) => {
          const id = Math.floor(Math.random() * 10000) + 1
          const newTask = { id, ...task}
          setTasks([...tasks, newTask])
      }


	//   Delete Task
	const deleteTask = (id) => {
		setTasks(tasks.filter((task) => task.id !== id))
	}

    //Toggle Reminder
    const toggleReminder = (id) => {
        setTasks(tasks.map((task) => task.id === id
        ? {...task,reminder: !task.reminder} : task
        ))
    }



return (

	<div className='container blue-color'>
		
		<Header name="Task Manager" onAdd={()=> setShowAddTask(!showAddTask)}  showAdd={showAddTask} />
       { showAddTask && <AddTask onAdd={addTask}/>}
	{ tasks.length > 0 ? <Tasks tasks={tasks} onDelete={deleteTask} onToggle={toggleReminder}/> : 'No Task remaining'}
	</div>
);
}

export default App;
